import Header from "../components/Layout/Header";
import Footer from "../components/Layout/Footer";
import "./css/Home.css";
import Slide from "./Slide";

const Home = () => {
    return (
        <div className="home">
            <Header />
            <main className="main">
                <Slide />
            </main>
            <img classname="kakaotalk" alt="kakaotalk" src="img/kakaotalk.png"/>
            <Footer />
        </div>
    )
}

export default Home