import './css/Slide.css';
import { Swiper, SwiperSlide } from 'swiper/react';
import React from 'react';
import SwiperCore, { Pagination, Autoplay } from 'swiper';
import 'swiper/swiper.scss';
import 'swiper/components/pagination/pagination.scss';

SwiperCore.use([Pagination, Autoplay]);

const Slide = () => {
    return(
        <div className="slide">
            <Swiper
                className="swiper"
                spaceBetween={80}
                slidesPerView={1}
                pagination={{ clickable: true }}
                autoplay={{ delay: 1500}}
            >
                <SwiperSlide><img alt="slider1" src='img/slider/slider1.jpg'/></SwiperSlide>
                <SwiperSlide><img alt="slider2" src='img/slider/slider2.jpg'/></SwiperSlide>
                <SwiperSlide><img alt="slider3" src='img/slider/slider3.jpg'/></SwiperSlide>
                <SwiperSlide><img alt="slider4" src='img/slider/slider4.jpg'/></SwiperSlide>
            </Swiper>
        </div>
    )
}

export default Slide